import * as React from 'react';
import { Box, Button } from '@chakra-ui/core';
import { useMotionValue, motion } from 'framer-motion';

const Container = React.forwardRef({ children, width, index }, (ref) => {
  return (
    <Box ref={ref} display="flex" width={`${width * children.length}`} transform={`translateX(-${width * index})`}>
      {children}
    </Box>
  );
});

const MotionContainer = motion.custom(Container);

export default function Carousel({ children, width = 500 }) {
  const x = useMotionValue();
  const [index, setIndex] = React.useState(0);
  console.log(index);
  return (
    <Box position="relative" width={width} mx="auto" my={4}>
      <Box position="relative" overflow="hidden" maxW="100%" w="100%" zIndex="1">
        <MotionContainer>
          {children.map((item, i) => (
            <Box key={i} flex="1" overflow="hidden" minW={width}>
              {item}
            </Box>
          ))}
        </MotionContainer>
      </Box>
      <Box position="absolute" top="0" right="0" bottom="0" left="0" w="100%" h="100%" pointerEvents="none">
        <Button
          pointerEvents="auto"
          position="absolute"
          top="50%"
          right="0"
          transform="translateY(-50%) translateX(50%)"
          onClick={() => setIndex(index >= children.length - 1 ? index : index + 1)}
        >
          next
        </Button>
        <Button
          pointerEvents="auto"
          position="absolute"
          top="50%"
          left="0"
          transform="translateY(-50%) translateX(-50%)"
          onClick={() => setIndex(index <= 0 ? index : index - 1)}
        >
          prev
        </Button>
      </Box>
    </Box>
  );
}
